import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import { View, ScreenSpinner, AdaptivityProvider, AppRoot, ConfigProvider } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Menu from './panels/Menu';
import Top from './panels/Top';
import Store from './panels/Store';


const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);

	useEffect(() => {
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	const [likes, setLikes] = useState(0);

	function increment() {
		setLikes(likes + 1);
	}

	return (
		<ConfigProvider scheme="space_gray">
			<AdaptivityProvider>
				<AppRoot>
					<View activePanel={activePanel} popout={popout}>
						<Home id='home' fetchedUser={fetchedUser} go={go} likes={likes} increment={increment}/>
						<Menu id='menu' fetchedUser={fetchedUser} go={go} />
						<Top id='top' go={go} />
						<Store id='store' go={go} />
					</View>
				</AppRoot>
			</AdaptivityProvider>
		</ConfigProvider>
	);
}

export default App;
